import { Location } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { fadeInRight400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { AgentService, RequestResource } from '@core/http';
import { FileDownload, FileService, UriService  } from '@core/utils';
import { FilterEvent, RequestIndexResponse } from '@data/interfaces';
import { FileDownloadRepresentation } from '@data/interfaces';
import { Project, Request } from '@data/schema';
import { RequestsCreateComponent } from '@requests/components/requests-create/requests-create.component';
import { RequestsEditComponent } from '@requests/components/requests-edit/requests-edit.component';

import { LayoutConfigService } from '@layout/services/layout-config.service';
import { RequestDataService } from './services/request-data.service';
import { RequestsIndexIcons } from './services/requests-index-icons.service';
import { RequestsTableConfig } from './services/requests-table-config.service';

@Component({
  selector: 'requests-index',
  styleUrls: ['./requests-index.component.scss'],
  templateUrl: './requests-index.component.html',
  animations: [
    stagger40ms,
    scaleIn400ms,
    fadeInRight400ms,
  ],
})
export class RequestsIndexComponent implements OnInit, OnDestroy {
  crumbs = []; // Breadcrumb settings
  menuOpen = false;
  pollRequestsInterval;

  project: Project;
  indexParams: any; // Current index params

  // Table
  requests: Request[];
  totalRequests: number;
  page: number;
  pageSize: number;
  filter: string;

  // activeCategory: 'frequently' | 'starred' | 'all' | 'high' | 'medium' | 'low' | 'none' = 'all';
  tableColumns: TableColumn<Request>[];

  constructor(
    public icons: RequestsIndexIcons,
    private activatedRoute: ActivatedRoute,
    private agentService: AgentService,
    private dialog: MatDialog,
    private file: FileService,
    private fileDownload: FileDownload,
    private layoutConfigService: LayoutConfigService,
    private location: Location,
    private requestDataService: RequestDataService,
    private requestResource: RequestResource,
    private requestsTableConfig: RequestsTableConfig,
    private router: Router,
    private route: ActivatedRoute,
    private uri: UriService
  ) { }

  ngOnInit() {
    const routeSnapshot = this.route.snapshot;

    this.project = routeSnapshot.data.project;
    this.requestsTableConfig.updateProjectId(this.project.id);

    const requests: RequestIndexResponse = routeSnapshot.data.requests;
    this.requests = requests.list;
    this.totalRequests = requests.total;

    this.indexParams = { ...routeSnapshot.queryParams };
    this.indexParams.page = this.indexParams.page || 0;
    this.indexParams.size = this.indexParams.size || 20;
    this.page = this.indexParams.page;
    this.pageSize = this.indexParams.size;
    this.filter = this.indexParams.filter;

    this.requestsTableConfig.updatePageSize(this.pageSize);
    this.requestsTableConfig.updatePageIndex(this.page);

    this.crumbs.push({ name: this.project.name });
    this.crumbs.push({ name: 'Requests' });

    this.tableColumns = this.buildTableColumns();

    if (this.layoutConfigService.isProxied()) {
      this.pollRequestsInterval = setInterval(() => {
        this.agentService.showStatus('requests-modified').subscribe(res => {
          if (res && res === this.project.id) {
            console.log('New requests detected...');
            this.getRequests();
          }
        });
      }, 10000);
    }
  }

  ngOnDestroy() {
    if (this.pollRequestsInterval) {
      clearInterval(this.pollRequestsInterval);
    }
  }

// API Access

  createRequest(data: FormData) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    data.append('project_id', project_id);

    this.requestResource.create(data).subscribe(
      (res: RequestIndexResponse) => {
        this.requests = res.list.concat(this.requests);
      },
      error => {

      }
    );
  }

  getRequests(params: any = this.indexParams) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    params.project_id = project_id;

    this.requestResource.index(params).subscribe(
      (res: RequestIndexResponse) => {
        this.requests = res.list;
        this.totalRequests = res.total;

        this.updateUrlQueryParams(params);
      },
      error => {

      }
    );
  }

  updateRequest(id: Request['id'], data) {
    this.requestResource.update(id, data).subscribe(
      res => {
        this.getRequests();
      },
      error => {

      });
  }

  deleteRequest(requestId: Request['id']) {
    this.requestResource.destroy(requestId).subscribe(res => {
      this.requests = this.requests.filter((request: Request) => {
        return request.id !== requestId;
      });
    });
  }

  downloadRequest(requestId: Request['id']) {
    this.requestResource.download(requestId).subscribe((res: FileDownloadRepresentation) => {
      this.fileDownload.create(res);
    });
  }

  searchRequests(queryString: String) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    this.indexParams.page = 0;
    this.page = 0;

    if (!queryString.length) {
      delete this.indexParams.q;
    } else {
      this.indexParams.q = queryString;
    }

    this.getRequests();
  }

  sortRequests(event) {
    const column: string = event.active;
    const direction: string = event.direction; // desc or asc

    if (!direction) {
      delete this.indexParams.sort_by;
      delete this.indexParams.sort_order;
    } else {
      this.indexParams.sort_by = column;
      this.indexParams.sort_order = direction;
    }

    this.getRequests();
  }

  filterRequests($event: FilterEvent) {
    if (!$event.filter) {
      delete this.indexParams.filter;
    } else {
      this.indexParams.filter = $event.filter;
    }

    this.getRequests();
  }

 // View Access

  openCreateDialog() {
    const dialogRef = this.dialog.open(RequestsCreateComponent, {
      width: '750px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createRequest($event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  openEditDialog(request: Request) {
    const dialogRef = this.dialog.open(RequestsEditComponent, {
      data: request,
      width: '600px',
    });

    const onEditSub = dialogRef.componentInstance.onEdit.subscribe(($event) => {
      this.updateRequest(request.id, $event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onEditSub.unsubscribe();
    });
  }

  viewRequest(requestId: Request['id']) {
    const request: Request = this.requests.find((candidate: Request) => {
      return candidate.id === requestId;
    });

    this.requestDataService.set(request);

    const uri = new this.uri.class(this.location.path());
    const path: string = this.file.join(uri.pathname, requestId);

    uri.pathname = path;

    this.router.navigateByUrl(uri.pathname + uri.query);
  }

  toggleRequestStar(id: Request['id']) {
    const request = this.requests.find(s => s.id === id);

    if (request) {
      request.starred = !request.starred;
      this.updateRequest(id, request);
    }
  }

  closeMenu() {
    this.menuOpen = false;
  }

  // For when the screen size is small
  openMenu() {
    this.menuOpen = true;
  }

 /*
  *
  * $event = {
  *   previousPageIndex: 0
  *   pageIndex: 0
  *   pageSize: 50
  *   length: 3
  * }
  *
  */
  handlePaginateChange($event) {
    const curIndex = this.requestsTableConfig.pageIndex;
    const curSize = this.requestsTableConfig.pageSize;
    const newIndex = $event.pageIndex;
    const newSize = $event.pageSize;

    if (curSize !== newSize) {
      this.pageSize = newSize;
      this.requestsTableConfig.updatePageSize(newSize);
    }

    if (curIndex != newIndex) {
      this.page = newIndex;
      this.requestsTableConfig.updatePageIndex(newIndex);
    }

    if (curIndex != newIndex || curSize !== newSize) {
      this.indexParams.page = newIndex;
      this.indexParams.size = newSize;
      this.getRequests();
    }
  }

// Helpers

  updateUrlQueryParams(newQueryParams) {
    const queryParams = { ...this.indexParams };

    Object.entries(newQueryParams).forEach(([key, value]) => {
      queryParams[key] = value;
      this.indexParams[key] = value;
    });

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  buildTableColumns(): TableColumn<Request>[] {
    return [
      {
        label: '',
        property: 'selected',
        type: 'checkbox',
        cssClasses: ['w-6'],
        visible: true,
        canHide: false,
      },
      {
        label: 'Method',
        property: 'method',
        type: 'text',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Host',
        property: 'host',
        type: 'text',
        cssClasses: ['font-medium'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Path',
        property: 'path',
        type: 'text',
        cssClasses: ['font-medium'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Status',
        property: 'status',
        type: 'custom',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Latency',
        property: 'latency',
        type: 'custom',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Created At',
        property: 'created_at',
        type: 'date',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
      },
      {
        label: '',
        property: 'starred',
        type: 'toggleButton',
        cssClasses: ['text-secondary', 'w-10'],
        visible: false,
        canHide: false,
        icon: (scenario) => scenario.starred ? this.icons.icStar : this.icons.icStarBorder,
      },
      {
        label: '',
        property: 'menu',
        type: 'menuButton',
        cssClasses: ['text-secondary', 'w-10'],
        visible: true,
        canHide: false,
      },
    ];
  }
}
