import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import icBusiness from '@iconify/icons-ic/twotone-business';
import icClose from '@iconify/icons-ic/twotone-close';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icLabel from '@iconify/icons-ic/twotone-label';
import icEmail from '@iconify/icons-ic/twotone-mail';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icPerson from '@iconify/icons-ic/twotone-person';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPrint from '@iconify/icons-ic/twotone-print';

import { Request, RequestPriorities, RequestPriorityData } from '@data/schema';

@Component({
  selector: 'requests-edit',
  templateUrl: './requests-edit.component.html',
  styleUrls: ['./requests-edit.component.scss'],
})
export class RequestsEditComponent implements OnInit {

  form = this.fb.group({
    name: null,
    description: null,
  });

  onEdit = new EventEmitter<object>();

  // icons
  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icBusiness = icBusiness;
  icPerson = icPerson;
  icEmail = icEmail;
  icPhone = icPhone;
  icLabel = icLabel;

  requestPriorities = RequestPriorityData;
  selectedPriority;

  constructor(
    @Inject(MAT_DIALOG_DATA) public request: Request,
    private dialogRef: MatDialogRef<RequestsEditComponent>,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.form.patchValue(this.request);

    const priorityIndex = this.requestPriorities.length - this.request.priority - 1;

    this.selectedPriority = this.requestPriorities[priorityIndex];
  }

  save() {
    const form = this.form.value;

    form.priority = this.selectedPriority.value;

    this.onEdit.emit(form);
    this.dialogRef.close();
  }

  handlePrioritySelect(priority) {
    this.selectedPriority = priority;
  }
}
