import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class RequestsTableConfig {
  projectId: number;

  data = [];
  private dataSubject = new BehaviorSubject(this.data);
  data$ = this.dataSubject.asObservable();

  pageSize = 20;
  private pageSizeSubject = new BehaviorSubject(this.pageSize);
  pageSize$ = this.pageSizeSubject.asObservable();

  pageIndex = 0;
  private pageIndexSubject = new BehaviorSubject(this.pageIndex);
  pageIndex$ = this.pageIndexSubject.asObservable();

  pages = 0;
  private pagesSubject = new BehaviorSubject(this.pages);
  pages$ = this.pagesSubject.asObservable();

  constructor() { }

  updateProjectId(id) {
    this.projectId = id;
  }

  updateData(data) {
    this.dataSubject.next(data);
    this.data = data;
  }

  updatePageSize(size: number) {
    this.pageSizeSubject.next(size);
    this.pageSize = size;
  }

  updatePageIndex(index: number) {
    this.pageIndexSubject.next(index);
    this.pageIndex = index;
  }

  updatePages(pages: number) {
    this.pagesSubject.next(pages);
    this.pages = pages;
  }
}
